.desktop-navigation-aside
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: left;
    align-items: left;
    padding: 0 1.5rem;
    height: calc(100vh - 8rem);
    max-width: 25rem;
    position: sticky;
    top: 4rem;
    
    .navigationLink
    {
        display: block;
        text-align: right;
        font-size: 6rem;
        line-height: 6rem;
        font-family: "DM Serif Text";
        font-weight: 100;
        font-style: italic;
        color: unset;
        letter-spacing: -3px;
        text-decoration: none;
        display: block;
        padding: 10px;
        overflow: hidden;
        text-wrap: nowrap;

        span 
        {
            display: inline-block;
            transition: right 250ms var(--BOUNCEBREZIER);
            position: relative;
            right: 0;
            margin: 0;
            line-height: 0;
        }

        &:hover span 
        {
            right: 10px;
        }
    }
}

@media (max-width: 800px) {
    #root > .content 
    {
        gap: 20px;
    }

    .desktop-navigation-aside 
    {
        max-width: 10rem;
        margin-left: -3rem;

        .navigationLink
        {
            font-size: 3rem;
            line-height: 3rem;
        }
    }
}

@media (max-width: 600px) {
    .desktop-navigation-aside
    {
        display: none;
    }
}