.projectCards
{
    display: flex;
    flex-direction: column;
    margin-top: 1rem;
    gap: 1.5rem;
}

.projectCard
{
    box-shadow: 0 0 15px #111212;
    border: 1px solid #111212;
    background-color: var(--BACKGROUNDDARKER);
    border-radius: 10px;
    box-sizing: border-box;
    padding: 1.5rem;
    cursor: pointer;

    p
    {
        font-size: 1rem;
        max-height: 4.5rem;
        overflow: hidden;
        position: relative;

        /* &::after
        {
            content: "";
            position: absolute;
            bottom: 0;
            right: 0;
            display: block;
            width: 150px;
            height: 1.5rem;
            background: linear-gradient(to right, transparent, var(--BACKGROUNDDARKER));
        } */
    }

    .caption
    {
        font-size: 0.8rem;
        line-height: 0.8rem;
        margin: 5px 0;
    }

    .projectTags
    {
        display: flex;
        gap: 8px;
        flex-wrap: wrap;

        .tag 
        {
            color: var(--SECONDARYCOLOR);
            background-color: var(--BACKGROUNDLIGHTER);
            padding: 4px 10px;
            height: 26px;
            box-sizing: border-box;
            border-radius: 13px;
            font-size: 0.9rem;
            text-wrap: nowrap;
        }
    }
}