@import url('https://fonts.googleapis.com/css2?family=DM+Serif+Text:ital@0;1&display=swap');
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&display=swap');

@import url(./Components/Sections/sections.css);

:root
{
    --BACKGROUNDLIGHTER: #282b29;
    --BACKGROUND: #1e201f;
    --BACKGROUNDDARKER: #191a19;
    --BACKGROUNDBLURRED: #1e201fd2;
    --TEXT: #30e289;
    --SECONDARYCOLOR: #ccc;
    --TERTIARYCOLOR: #aaa;

    --BOUNCEBREZIER: cubic-bezier(0.3, 2, 0.4, 1);

    background-color: var(--BACKGROUND);
}

body
{
    background-color: var(--BACKGROUND);
    color: var(--TEXT);
    font-family: "DM Sans";
    margin: 0;
    width: 100vw;
    overflow-x: hidden;
    overflow-y: scroll;
}

#root > .content
{
    max-width: min(calc(100vw - 8rem), 1000px);
    margin: auto;
    min-height: 100vh;
    display: flex;
    gap: 60px;
}

aside
{
    flex: 1;
}

h1
{
    line-height: 3rem;
    font-size: 1.1rem;
    text-transform: uppercase;
    color: var(--TEXT);
}

h2
{
    line-height: 1rem;
    font-size: 1rem;
    margin: 0;
    text-transform: uppercase;
    color: #fff;
}

p:not(.caption)
{
    color: var(--SECONDARYCOLOR);
    font-size: 1.1rem;
    line-height: 1.5rem;
}

p.caption 
{
    color: var(--TERTIARYCOLOR);
    font-size: 0.8rem;
    text-wrap: balance;
    font-weight: 300;

    strong {
        font-weight: 900;
        color: var(--SECONDARYCOLOR);
    }
}

@media (max-width: 600px) {
    #root > .content
    {
        max-width: calc(100vw - 2.5rem);
    }
}