section 
{
    padding: 4rem 0 2rem 0;

    h1 
    {
        opacity: 0;
        height: 0;
        margin: 0;
    }
}

@media (max-width: 600px) {
    section 
    {
        padding: 2rem 0;
        
        h1 
        {
            opacity: 1;
            height: unset;
            margin: unset;
            padding: 0 20px;
            margin: 0 -20px;
            
            background-color: var(--BACKGROUNDBLURRED);
            position: sticky;
            top: 0;
            backdrop-filter: blur(10px);
        }
    }
}